import React from 'react';
import BumpsChart from '../components/BumpsChart';
import styled from 'styled-components';

const Description = styled.h3`
  font-size: 1.2rem;
  padding-top: 6px;
  text-align: center;
`;

export default ({ data }) => {
  return (
    <div>
      <Description>
        {data.resultsJson.set} - {data.resultsJson.gender}
      </Description>
      <BumpsChart
        data={data.resultsJson}
        selectedCrews={new Set()}
        highlightCrew={crew => console.log(crew)}
        selectYear={year => console.log(year)}
      />
    </div>
  );
};

export const query = graphql`
  query ResultsQuery($slug: String!) {
    resultsJson(fields: { slug: { eq: $slug } }) {
      set
      gender
      small
      crews {
        gender
        name
        set
        values {
          day
          pos
        }
        valuesSplit {
          blades
          day
          gender
          name
          set
          spoons
          values {
            day
            pos
          }
        }
      }
      divisions {
        divisions {
          year
          start
          size
        }
        gender
        set
        year
      }
      startYear
      endYear
      maxCrews
    }
  }
`;



